module.exports = "<article class=entry> <header class=entry-header> <h2 class=entry-title> <router-link to=/webcomponents-base>Web Components. Основы</router-link> </h2> <div class=entry-meta> <ul> <li>3 июля 2017</li> </ul> </div> </header>  <div class=entry-content> <p class=lead> В данной статье будут рассмотрены <strong>базовые вещи касательно Web Components</strong>. <ul class=disc> <li>Способы создания элементов веб-страницы на основе Web Components.</li> <li>Четыре спецификации Web Components.</li> <li>Фундаментальные аспекты компонентов.</li> </ul> </p> </div> </article>";

