module.exports = "<article class=entry> <header class=entry-header> <h2 class=entry-title> <router-link to=/webcomponents-principles>Web Components. Принцип использования</router-link> </h2> <div class=entry-meta> <ul> <li>3 августа 2017</li> </ul> </div> </header>  <div class=entry-content> <p class=lead> По ходу статьи <strong>рассмотрим вопросы использования Web Components в приложении</strong>, какие существуют подходы, их плюсы и минусы. Увидим, как подключить компонент через HTML Imports и JavaScript. </p> </div> </article>";

